
body {
  margin: 0;
  padding: 0;
}
img {
  vertical-align: top;
}
#app {
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "palt" 1;
}
.aside {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10;
}
.aside a {
  display: block;
}
.section {
  position: relative;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  background-position: 50% 50%;
  background-size: cover;
  color: #fff;
}
.section:first-of-type {
  position: fixed;
  top: 0;
  left: 0;
}
.section:first-of-type + .section {
  margin-top: 100vh;
}
.section .figure {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-position: 50% 50%;
  background-size: cover;
}
.section .figure img {
  display: none;
  width: 100%;
  height: auto;
}
.footer {
  position: relative;
  padding: 10px;
  background: #000;
  color: #fff;
  text-align: center;
}
.footer p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: .8rem;
  line-height: 15px;
}
.footer p a,
.footer p img {
  display: inline-block;
}
.effect-figure-enter-active,
.effect-figure-leave-active {
  transition-property: opacity;
  transition-delay: 400ms;
  transition-duration: 600ms;
  transition-timing-function: ease;
}
.effect-figure-enter,
.effect-figure-leave-to {
  opacity: 0;
}
.effect-figure-leave,
.effect-figure-enter-to {
  opacity: 1;
}
.effect-caption-enter-active,
.effect-caption-leave-active {
  transition-property: opacity, transform;
  transition-delay: 100ms;
  transition-duration: 400ms;
  transition-timing-function: ease;
}
.effect-caption-enter,
.effect-caption-leave-to {
  opacity: 0;
  transform: translate3d(0, 33%, 0);
}
.effect-caption-leave,
.effect-caption-enter-to {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}



